































































































































































































































import { Component, Vue } from 'vue-property-decorator';
import { getFilms } from '@/components/FilmsList'
import Film from '@/models/FilmModel.ts';

@Component({})
export default class Home extends Vue {
  selected: string = 'map';
  continent: string = '';
  type: string = 'Other';
  minYear: string = "2017";
  maxYear: string = "2020";
  minRating: number = 0;
  maxRating: number = 9;
  rating: string = "";
  rotateCenters: string[] = [", 154, 154)", ", 104, 103)", ", 55, 54)"];
  rotates: string[] = ["rotate(0)", "rotate(0)", "rotate(0)"];
  films: Film[] = [{title: "", flag1: "none", flag2: "none", flag3: "none", length: 0},
                  {title: "", flag1: "none", flag2: "none", flag3: "none", length: 0},
                  {title: "", flag1: "none", flag2: "none", flag3: "none", length: 0}];
  href: string = '';
  svgStyles : string = "<svg xmlns='http://www.w3.org/2000/svg' class='generated_img'><style>.generated_img{height: 700px;width: 496px;}.logo_poster{margin-left: 300px;}.poster_text{font-family: 'Nunito Sans', sans-serif;font-family: 'Nunito Sans', sans-serif;font-size: 18px;fill: white;}</style><rect width='100%' height='100%' style='fill: DarkSlateBlue;'></rect>";

  setType(t: string) {
    this.type = t;
  }
  setSelected(sel: string) {
    this.selected = sel;
  }
  setContinent(c: string) {
    this.continent = c;
    this.setSelected('preview');
  }
  setRating(min: number, max: number) {
    this.minRating = min;
    this.maxRating = max;
    this.rating = String(min);
  }
  generate() {
    let selFilms: Array<Film> = getFilms(this.continent, this.type, Number(this.minYear), 
                                        Number(this.maxYear), this.minRating, this.maxRating);
    for(let i=0; i<3; i++){
      if(selFilms.length > i) {
        Vue.set(this.films, i, selFilms[i]);
        Vue.set(this.rotates, i, "rotate(" + selFilms[i]["length"] + this.rotateCenters[i]);
      }
      else {
        Vue.set(this.films, i, {title: "", flag1: "none", flag2: "none", flag3: "none", length: 0});
        Vue.set(this.rotates, i, "rotate(0)");
      }
    }
  }
  modifyHref() {
    const svgContent = this.svgStyles + new XMLSerializer().serializeToString((this.$refs.svg as SVGElement)) + "</svg>";
    const blob = new Blob([svgContent], {type: "image/svg+xml"});
    this.href = window.URL.createObjectURL(blob);
  }
  pic_export() {
    this.setSelected('pic_export');
  }
}
